.button {
  display: flex;
  justify-content: center;
  align-items: center;
  
  height: 56px;
  width: 100%;
  background-color: $accent-main-color;
  color: #fff;
  font-weight: 700;
  transition: background-color $effectDuration;

  &:hover {
    background-color:#AE1C23;
  }

  &:active {
    background-color:#EB343D;
  }
}